/* Your existing styles for larger screens */

.about {
  background-color: white;
  padding: 4rem;
  color: #333;
  display: flex;
  justify-content: center;
  align-items: center;
  margin-top: 30px;
}

.about-content {
  max-width: 100%;
  display: flex;
  justify-content: space-between;
  align-items: left;
  flex-direction: row; /* Ensure a horizontal layout for larger screens */
}

.about-image {
 
  text-align: center;
  margin-top: 40px;
  order: 1; /* Image should appear first on larger screens */
}

.profile-image {
  width: 320px;
  height: 320px;
  object-fit: cover;
  border-radius: 50%;
  box-shadow: 0px 0px 20px rgba(0, 0, 0, 0.3);
  transition: transform 0.3s ease-in-out;
  z-index: 1;
  position: relative;
}

.profile-image:hover {
  transform: scale(1.1);
}

.about-description {
  width: calc(70% - 20px);
  padding: 0rem;
  order: 2; /* Content should appear second on larger screens */
}

h1 {
  font-size: 22px;
  margin-top: 5px;
}

.info-box {
  background-color: #f0f0f0;
  padding: 20px;
  border-radius: 10px;
  margin-left: 0px;
  width: 100%;
}

.info-item {
  display: flex;
  align-items: center;
  margin-bottom: 5px;
  padding: 0px;
  margin-right: 1px;
}

.info-icon {
  font-size: 20px;
  margin-right: 10px;
  color: rgb(68, 68, 182);
}

.info-text {
  font-size: 1rem;
  color: black;
  font-size: 18px;
  text-align: justify;
  margin-left: 10px;
  margin-right: 10px;
}

/* Media Query for Responsiveness */
@media (max-width: 1100px) {
  .about-content {
    flex-direction: column;
    text-align: center;
  }

  .about-image {
    margin-top: 100px; /* Adjust as needed */
    position: relative;
    order: 1; /* Image should appear first on smaller screens */
  }

  .profile-image {
    position: relative;
    top: 0;
    left: 0;
    transform: none;
    margin: 0 auto; /* Center the image horizontally on smaller screens */
    display: flex;
    justify-content: center;
  }

  .about-description {
    width: 95%;
    padding: 5px;
    text-align: left;
    margin-bottom: 10px;
    margin-top: 10px;
    order: 2; /* Content should appear second on smaller screens */
  }

  h1 {
    font-size: 22px;
    margin: 7px;
    width: 80%;
  }

  .info-icon {
    font-size: 20px;
  }

  .info-box {
    margin-left: 0;
    width: 100%;
    margin-top: 10px;
  }
}
