.myself-container {
  background-color: #f0f0f0;
  padding: 2rem;
  color: black;
  width: 90%;
  margin: 0 auto; /* Center the box horizontally */
  border-radius: 20px;
  text-align: left;
}

.myself-section {
  margin-bottom: 2rem;
}

.myself-heading {
  font-size: 10px;
  color: rgb(83, 83, 94);
  text-align: left;
}

.myself-paragraph {
  text-align: left;
  margin: 0 10px; /* Equal margin on both sides */
  font-size: 40px;
}

.headings {
  color: rgb(68, 68, 182);
  text-align: left;
}

.paragraph-text {
  text-align: justify;
  margin: 0 10px; /* Equal margin on both sides */
  font-size: 20px;
}

/* Media Query for Smaller Screens (e.g., mobile) */
@media (max-width: 600px) {
  .myself-heading {
    font-size: 1.2rem;
  }
  .myself-container {
    width: 75%;
  }
  .myself-paragraph {
    font-size: 20px;
  }
}
